import React, { useEffect, useState } from 'react';
import parse from 'html-react-parser';

import './App.scss';
import content from './content';
import profile from 'images/profile.jpg';

const c = content;
const breakMd = 768;
const breakSm = 576;

const printMode = false;

const expMidIdx = Math.ceil(c.experiences.length / 2);
const expPart1 = c.experiences.slice(0, expMidIdx);
const expPart2 = c.experiences.slice(expMidIdx, c.experiences.length);

const projMidIdx = Math.floor(c.projects.length / 2);
const projPart1 = c.projects.slice(0, projMidIdx);
const projPart2 = c.projects.slice(projMidIdx, c.projects.length);

const perProjMidIdx = Math.ceil(c.personalProjects.length / 2);
const perProjPart1 = c.personalProjects.slice(0, perProjMidIdx);
const perProjPart2 = c.personalProjects.slice(perProjMidIdx, c.personalProjects.length);


function emphasizeWords(str: string, words: string[]) {
  let result = str;

  // Use a loop to replace each word or phrase
  words.forEach(word => {
      // Use a regular expression to match the word and replace it
      let regex = new RegExp(escapeRegExp(word), 'g');
      result = result.replace(regex, `<strong>${word}</strong>`);
  });

  return result;
}

// Utility function to escape special characters in a string for use in a regex
function escapeRegExp(str: string) {
  return str.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'); // $& means the whole matched string
}

const contactArray = ([
  {
    icon: 'fas fa-home',
    element: <>{c.contact.residence}</>
  },
  {
    icon: 'fab fa-linkedin-in',
    element: <a href={`https://${c.contact.linkedin}`} className='contact-a'>{c.contact.linkedin}</a>
  },
  {
    icon: 'fab fa-github',
    element: <a href={`https://${c.contact.github}`} className='contact-a'>{c.contact.github}</a>
  },
  {
    icon: 'fas fa-envelope-square',
    element: <>{c.contact.email}</>
  },
  printMode ?
  {
    icon: 'fas fa-phone-square',
    element: <>{c.contact.phone}</>
  } : null
].filter(a => a != null));

function App() {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    // Update the window width when the window is resized
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    // Attach the event listener
    window.addEventListener('resize', handleResize);

    // Clean up the event listener on component unmount
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <div className="container-fluid">
      <div className="container profile-container">

        <div className="pro-s-cover">
          <div className='row' style={{width:'100%', marginLeft:'0px', marginRight:'0px'}}>
            <div className='col-md-4'>
              <img src={profile} alt="" />
              <div className='name'>{c.name}</div>
              <span>{c.jobTitle}</span>
            </div>
            <div className='col-md-2' style={{ 
              //borderLeft:'1px solid #CCC', 
              //borderRight:'1px solid #CCC', 
              display:'flex', flexDirection:'column'
            }}>
              {windowWidth < breakMd ?
                <>
                  <div className="num-box w-100 d-flex">
                    <div className="hire">
                      <b>11 years</b> <br />
                      <span>.Net Experience</span>
                    </div>
                    <div className="msg">
                      <b>5 years</b> <br />
                      <span>Work Experience</span>
                    </div>
                  </div>
                  <div className="custom-button-box">
                  </div>
                </> :
                <>
                  <div style={{flex:'1', display:'flex', alignItems:'center', justifyContent:'center'}}>
                    <div>
                      <b>11 years</b><br />
                      <span>.Net Experience </span>
                    </div>
                  </div>
                  <div style={{flex:'1', display:'flex', alignItems:'center', justifyContent:'center'}}>
                    <div>
                      <b>5 years</b><br />
                      <span>Work Experience</span>
                    </div>
                  </div>
                </>
              }
            </div>
            <div className='col-md-2'></div>
            <div className='col-md-4' style={{
              display:'flex', flexDirection:'column', justifyContent:'center',
              textAlign:'left',
            }}>
              <div className='row contact-container contact-container-sm'>
                {contactArray.map((a, i) => (
                  <div
                    key={i}
                    className={windowWidth < breakMd ?'col-md-4 col-sm-6' : 'col-12'}
                    style={{display:'flex'}}
                  >
                    <div style={{width:'25px'}}>
                      <i className={a?.icon} /> 
                    </div>
                    <div style={{flex:'1'}}>
                      {a?.element}
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>

        <div className="data-box">
          <h2>About Me</h2>
          {c.aboutMe.map((a, i) => (
            <p key={i}>{parse(emphasizeWords(a, ['.Net Framework', '.Net 8', 'React', 'Unit Testing', 'AI tools', 'microservices', ]))}</p>
          ))}
          <div style={{display:'flex', marginTop:'16px', paddingLeft:'20px'}}>
            <div style={{fontSize:'38px'}}>
              <i className="fas fa-graduation-cap"></i>
            </div>
            <div style={{flex:'1',
              display:'flex', flexDirection:'column', 
              justifyContent:'center', textAlign:'left',
            }} className='education'>
              <p style={{marginBottom:'4px'}} className='highlight'>{c.education.title}</p>
              <p style={{marginBottom:'4px'}}>{c.education.school}, {c.education.graduationYear}</p>
            </div>
          </div>
        </div>

        <div className="data-box" style={{
          paddingBottom:'10px'
        }}>
          <h2>Tech Stack</h2>
          <div className='row'>
            {c.mainSkills.map((ms, i) => (
              <div key={i} 
                className='col-md-4 col-sm-6'
                style={{
                  marginBottom:'12px'
                }}
              >
                <h6>{ms.group}</h6>
                <div style={{height:'8px'}}></div>
                <table className='skill-table'>
                  <tbody>
                  {ms.list.map((l, j) => (
                    <tr key={j}>
                      <td style={{width:'128px'}}>{l.name}</td>
                      <td style={{
                        paddingLeft:'8px',
                        fontSize:'0.8rem',
                        letterSpacing:'4px'
                      }}>
                        {Array.from({ length: 5 }).map((_, i) => (
                          <React.Fragment key={i}>{i < l.rating ? '' : ''}</React.Fragment>
                        ))}
                      </td>
                    </tr>
                  ))}
                  </tbody>
                </table>
              </div>
            ))}
            <div
              className='col-md-4 col-sm-6'
              style={{
                marginBottom:'12px'
              }}
            >
              <h6>Other Skills</h6>
              <div style={{height:'8px'}}></div>
              <div style={{fontSize:'0.9rem'}}>{c.otherSkills.join(', ')}</div>
            </div>
          </div>
        </div>

        <div className="data-box" style={{
          paddingBottom:'16px'
        }}>
          <h2>Experience</h2>
          <div className='row'>
            <div className='col-md-6'>
              {expPart1.map((a, i) => (
                <Exp key={i} i={i} exp={a} />
              ))}
              {windowWidth < breakSm && (
                <div style={{height:'16px', width:'100%'}}></div>
              )}
            </div>
            <div className='col-md-6'>
              {expPart2.map((a, i) => (
                <Exp key={i} i={i} exp={a} />
              ))}
            </div>
          </div>
        </div>

        {printMode && (
          <div style={{height:'55px'}}></div>
        )}

        <div className="data-box service-box" style={{
          paddingBottom:'24px'
        }}>
          <h2>Featured Projects</h2>
          <p>{c.projectHeadine}</p>
          <div className='row'>
            <div className='col-md-6'>
              {projPart1.map((a, i) => (
                <Proj key={i} i={i} proj={a} 
                  last={i === projPart1.length - 1} />
              ))}
            </div>
            <div className='col-md-6'>
              {projPart2.map((a, i) => (
                <Proj key={i} i={i} proj={a} 
                 last={i === projPart2.length - 1}/>
              ))}
            </div>
          </div>
        </div>

        <div className="data-box service-box" style={{
          paddingBottom:'22px'
        }}>
          <h2>Personal Projects</h2>
          <div className='row'>
            <div className='col-md-6'>
              {perProjPart1.map((a, i) => (
                <PerProj key={i} i={i} proj={a} 
                  last={i === perProjPart1.length - 1}/>
              ))}
            </div>
            <div className='col-md-6'>
              {perProjPart2.map((a, i) => (
                <PerProj key={i} i={i} proj={a} 
                  last={i === perProjPart2.length - 1}/>
              ))}
            </div>
          </div>
        </div>

      </div>
    </div>
  );
}

function Exp(props: {
  i: number,
  exp: any
}){
  return (
    <div className={`row exp-row ${props.i === expPart1.length - 1 ? 'last' : ''}`}>
      <div style={{display:'flex', width:'100%', marginBottom:'4px'}}>
        <div style={{flex:'1'}}>
          <h6>{props.exp.position}</h6>
        </div>
        <div style={{width:'200px', textAlign:'right', lineHeight:'1rem'}}>
          <i>{props.exp.date}</i>
          <i style={{marginLeft:'16px'}}>
            {calculateDateRangeLength(props.exp.date)}
          </i>
        </div>
      </div>
      <div className='company-name'>{props.exp.company}</div>
      {props.exp.description && (
        <p>
          {parse(emphasizeWords(props.exp.description,
            ['consultant', 'modernizing', 'international team', 'solo developer', 'microservices']))
          }
        </p>
      )}
      
      {
        props.exp.vendors == null ? null :
        <div style={{width:'100%'}}>
          <div className='outsourcelabel' >
            *Outsourced From:
            <span style={{marginLeft:'8px'}}>{props.exp.vendors.join(', ')}</span>
          </div>
        </div>
      }
    </div>
  );
}

function Proj(props: {
  i: number,
  proj: any,
  last: boolean
}){
  return (
    <div key={props.i} className={`row exp-row ${props.last ? 'last' : ''}`}>
      <div style={{display:'flex', width:'100%'}}>
        <div style={{flex:'1'}}>
          <h6>{props.proj.name}</h6>
        </div>
      </div>
      <div style={{height:'4px', width:'100%'}}></div>
      <div style={{width:'100%'}}>
        <div className='techlabel' >
          Company :
          <span className='company-name' style={{marginLeft:'8px'}}>{props.proj.company}</span>
        </div>
      </div>
      <div style={{width:'100%'}}>
        <div className='techlabel' >
          Tech :
          <span style={{marginLeft:'8px'}}>
            {parse(emphasizeWords(props.proj.techs.join(', '), ['Docker', '.Net 6']))}
          </span>
        </div>
      </div>      
      <div style={{height:'4px', width:'100%'}}></div>
      <ul className='project-description'>
        {props.proj.points.map((a: string,i: number) => (
          <li key={i}>
            {parse(emphasizeWords(a, 
              ['Led weekly meetings', 'microservices', 'multi-tenant', 'scrape', 'SSO']))
            }
          </li>
        ))}
      </ul>
    </div>
  );
}

function PerProj(props: {
  i: number,
  proj: any,
  last: boolean
}){
  return (
    <div key={props.i} className={`row exp-row ${props.last ? 'last' : ''}`}>
      <div style={{display:'flex', width:'100%'}}>
        <div style={{flex:'1'}}>
          <h6>{props.proj.name}</h6>
        </div>
      </div>
      <div style={{height:'4px', width:'100%'}}></div>
      <table className='table-techlabel'>
        <tbody>
          <tr>
            <td>Tech</td>
            <td>:</td>
            <td>
              <span style={{marginLeft:'8px'}}>
                {parse(emphasizeWords(props.proj.techs.join(', '), 
                ['.Net 7', 'Docker', 'Google Cloud']))}
              </span>
            </td>
          </tr>
          {props.proj.repo && (
            <tr>
              <td>Repo</td>
              <td>:</td>
              <td>
                <a href={`https://${props.proj.repo}`}>
                  {props.proj.repo}
                </a>
              </td>
            </tr>
          )}
          {props.proj.url && (
            <tr>
              <td>Demo</td>
              <td>:</td>
              <td>
                <a className='highlight-link' href={`https://${props.proj.url}`}>
                  {props.proj.url}
                </a>
              </td>
            </tr>
          )}
        </tbody>
      </table>
      <div style={{height:'4px', width:'100%'}}></div>
      <ul className='project-description'>
        {props.proj.points.map((a: string,i: number) => (
          <li key={i}>
            {parse(emphasizeWords(a,
              ['Google SSO', 'demo', 'compresses', 'AI upscalers']))
            }
          </li>
        ))}
      </ul>
    </div>
  );
}

function calculateDateRangeLength(dateRange: string): string {
  // Split the input string into start and end dates
  const [start, end] = dateRange.split(' - ').map(date => {
      const [month, year] = date.split('/').map(Number);
      return new Date(year, month - 1); // month is 0-indexed in JavaScript Date
  });

  // Calculate the difference in years and months
  let yearsDifference = end.getFullYear() - start.getFullYear();
  let monthsDifference = (end.getMonth() + 1) - start.getMonth();

  if (monthsDifference < 0) {
      yearsDifference -= 1;
      monthsDifference += 12;
  }

  // Construct the result string
  if (yearsDifference > 0) {
    if(monthsDifference > 0)
      return `${yearsDifference}y ${monthsDifference}m`;
    else
      return `${yearsDifference}y`;
  } else {
      return `${monthsDifference}m`;
  }
}

export default App;
